  text-align: center;
  text-decoration: underline;
  font-family: "Josefin Sans";
  font-size: 30px;
  font-weight: 700;
  color: rgb(51, 51, 51);
}

.header {
  display: none;
}

.title-wrapper {
  text-align: left;
  width: 100%;
  margin-bottom: 3vh;
}

@media only screen and (min-width: 768px) {
  .header {
    height: 100vh;
    width: 100%;

    background-image: url(/repository-name/static/media/banner-ipad.4b64dc8e.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: top;

    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .title-wrapper {
    width: 100%;
    margin-bottom: 3vh;
  }
}

@media only screen and (min-width: 1000px) {
  .header {
    background-image: url(/repository-name/static/media/banner.6680cf27.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
  }

  .title-wrapper {
    width: 60%;
    /* padding: 50px 0px 50px 0px; */
  }
}

